.form {
	&.form--loading {
		position: relative;

		.spinner {
			display: block;
			position: absolute;
			width: 110%;
			height: 110%;
			max-height: 110%;
			max-width: 110%;
			left: -5%;
			top: -5%;

			> div {
				height: 40px;
				position: relative;
				top: 50%;
				margin-top: -20px; } } } }

.help-block {
	position: relative;
	z-index: 1;
	padding: 8px 6px;
	font-weight: $weight-semibold;
	font-size: 14px;
	margin: 4px 0 0;
	background: lighten($error-color, 60%);
	border: 1px solid lighten($error-color, 55%);
	color: $error-color;
	display: block;

	&:after, &:before {
		bottom: 100%;
		left: 10%;
		border: solid transparent;
		content: " ";
		height: 0;
		width: 0;
		position: absolute;
		pointer-events: none; }
	&:after {
		border-color: rgba($error-color, 0);
		border-bottom-color: lighten($error-color, 60%);
		border-width: 4px;
		margin-left: -4px; }
	&:before {
		border-color: rgba($error-color, 0);
		border-bottom-color: lighten($error-color, 55%);
		border-width: 5px;
		margin-left: -5px; } }

.spinner {
	display: none;
	width: 50px;
	height: 40px;
	text-align: center;
	font-size: 10px;
	background: rgba(255, 255, 255, 0.75);
	z-index: 1;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	&.spinner--visible {
		display: block; }

	> div {
		background-color: #333;
		height: 100%;
		width: 6px;
		display: inline-block;
		-webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
		animation: sk-stretchdelay 1.2s infinite ease-in-out; }

	.rect2 {
		-webkit-animation-delay: -1.1s;
		animation-delay: -1.1s; }
	.rect3 {
		-webkit-animation-delay: -1.0s;
		animation-delay: -1.0s; }
	.rect4 {
		-webkit-animation-delay: -0.9s;
		animation-delay: -0.9s; }
	.rect5 {
		-webkit-animation-delay: -0.8s;
		animation-delay: -0.8s; } }

@-webkit-keyframes sk-stretchdelay {
	0%, 40%, 100% {
		-webkit-transform: scaleY(0.4); }

	20% {
		-webkit-transform: scaleY(1); } }

@keyframes sk-stretchdelay {
	0%, 40%, 100% {
		transform: scaleY(0.4);
		-webkit-transform: scaleY(0.4); }

	20% {
		transform: scaleY(1);
		-webkit-transform: scaleY(1); } }

.label {
	&.label--full {
		display: block;
		width: 100%;
		margin-bottom: 5px;
		margin-top: 20px;

		&:first-of-type {
			margin-top: 0; } } }

label.label--error {
	display: block;
	margin-top: 10px;
	color: $error-color;
	font-weight: $weight-semibold; }

.button {
	vertical-align: middle;
	display: inline-block;
	text-align: center;
	text-decoration: none !important;
	font-weight: $weight-semibold;
	font-size: 15px;
	line-height: 1.4;
	border-radius: 0;
	padding: 7px 14px;
	border: none;
	cursor: pointer;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;

	&.button--regular {
		height: 40px;
		padding: 8px 30px;
		line-height: 1.5;

		&.button--small-padding {
			padding: 8px 15px; } }

	&.button--small {
		height: 36px;
		padding: 8px 20px;
		font-size: 14px;

		&.button--small-padding {
			padding: 8px 10px; } }

	&.button--full {
		width: 100%; }

	&.button--medium {
		height: 46px;
		padding: 10px 35px;
		font-size: 17px;
		line-height: 1.7;

		&.button--small-padding {
			padding: 10px 18px; } }

	&.button--large {
		height: 52px;
		padding: 12px 40px;
		font-size: 20px;

		&.button--small-padding {
			padding: 12px 20px; } }

	&.button--huge {
		height: 65px;
		padding: 18px 60px;
		font-size: 21px;

		&.button--small-padding {
			padding: 18px 30px; } }

	&.button--rounded {
		border-radius: 3px; }

	&.button--circular {
		border-radius: 100px; }

	&:disabled {
		pointer-events: none;
		cursor: not-allowed;
		background: $lighter-grey !important;
		color: $greyish !important; }

	&.button--double {
		line-height: 1.2;
		height: 70px;
		padding: 14px 50px;

		strong, span {
			display: block; }

		strong {
			font-size: 21px;
			font-weight: $weight-bold; }

		span {
			font-size: 15px;
			font-weight: $weight-default; } }

	&.button--green {
		background: $main-green;
		color: #fff;

		&:hover {
			background: $main-green-dark; } }

	&.button--grey {
		background: $dark-grey;
		color: $pale-grey;

		&:hover {
			background: $greyish-brown; } }

	&.button--light {
		background: $lightest-grey;
		color: $warm-grey;

		&:hover {
			background: $lighter-grey; } }

	&.button--grey-light {
		background: $lighter-grey;
		color: $warm-grey;

		&:hover {
			background: $light-grey; }

		&.button--border {
			background: transparent;
			border: 1px solid $lighter-grey;

			&:hover {
				background: $lighter-grey; } } }

	&.button--white {
		background: $white;
		color: $main-green;

		&:hover {
			background: $lightest-grey; } }

	&.button--text-grey {
		color: $greyish-brown; } }

button.button,
input.button {
	padding-top: 0 !important;
	padding-bottom: 0 !important; }

.input {
	vertical-align: middle;
	display: inline-block;
	padding: 8px 15px;
	color: $greyish-brown;
	font-size: 15px;
	border-radius: 0;
	line-height: 1.4;
	border: 1px solid $lighter-grey;
	background: $white;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;

	&:focus {
		box-shadow: 0 0 4px rgba(0, 0, 0, .3);
		border: 1px solid $greyish; }

	&.input--full {
		width: 100%; }

	&.input--semibold {
		font-weight: $weight-semibold; }

	&.input--regular {
		height: 40px;
		padding: 8px 12px; }

	&.input--transparent {
		background: transparent; }

	&.input--medium {
		height: 50px;
		padding: 13px 15px; }

	&.input--spacing {
		letter-spacing: 1px; }

	&.input--full {
		width: 100%;
		display: block; }

	&.input--uppercase {
		text-transform: uppercase; }

	&.input--plain {
		border: none !important; }

	&.input--success {
		background: lighten($main-green, 60%);
		border-color: rgba($main-green, 0.5);

		&:focus {
			box-shadow: 0 0 4px rgba($main-green, .3); } }

	&.input--error {
		background: lighten($error-color, 60%);
		border-color: rgba($error-color, 0.5);

		&:focus {
			box-shadow: 0 0 4px rgba($error-color, .3); } }

	&.input--no-number::-webkit-outer-spin-button,
	&.input--no-number::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0; } }

.select {
	vertical-align: middle;
	display: inline-block;
	padding: 8px 30px 8px 15px;
	color: $greyish-brown;
	font-size: 15px;
	border-radius: 0;
	line-height: 1.4;
	border: 1px solid $lighter-grey;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	background: $white url(/images/icons/icon-arrow-down-small.png) no-repeat right center;

	&:focus {
		box-shadow: 0 0 4px rgba(0, 0, 0, .3);
		border: 1px solid $greyish; }

	&.select--full {
		width: 100%; }

	&.select--semibold {
		font-weight: $weight-semibold; }

	&.select--regular {
		height: 40px; }

	&.select--medium {
		height: 50px;
		padding: 13px 30px 13px 15px; }

	&.select--spacing {
		letter-spacing: 1px; }

	&.select--full {
		width: 100%;
		display: block; }

	&.select--plain {
		border: none !important; }

	&.select--success {
		background-color: lighten($main-green, 60%);
		border-color: rgba($main-green, 0.5);

		&:focus {
			box-shadow: 0 0 4px rgba($main-green, .3); } }

	&.select--error {
		background-color: lighten($error-color, 60%);
		border-color: rgba($error-color, 0.5);

		&:focus {
			box-shadow: 0 0 4px rgba($error-color, .3); } } }

a {
	color: $greyish-brown;
	text-decoration: none; }

.selector {
	display: inline-block;
	border: 1px solid $light-grey;
	padding: 7px 0 7px 14px;
	font-size: 14px;

	ul {
		text-align: left;
		display: none;
		position: absolute;
		width: auto;
		background: $white;
		padding: 5px;
		border-radius: 3px;
		box-shadow: 0 0 7px rgba(0, 0, 0, .3);
		border: 1px solid #bbb;

		&:after, &:before {
			bottom: 100%;
			left: 50%;
			border: solid transparent;
			content: " ";
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none; }

		&:after {
			border-color: rgba(255, 255, 255, 0);
			border-bottom-color: #fff;
			border-width: 5px;
			margin-left: -5px; }

		&:before {
			border-color: rgba(187, 187, 187, 0);
			border-bottom-color: #bbb;
			border-width: 6px;
			margin-left: -6px; }

		li {
			display: block;

			&:hover {
				background: #f5f5f5; }

			a {
				display: block;
				padding: 10px; } } }

	&.selector--up {
		ul {
			top: auto;
			bottom: 100%;

			&:after, &:before {
				top: 100%;
				left: 50%;
				border: solid transparent;
				content: " ";
				height: 0;
				width: 0;
				position: absolute;
				pointer-events: none; }

			&:after {
				border-color: rgba(255, 255, 255, 0);
				border-top-color: #fff;
				border-width: 5px;
				margin-left: -5px; }

			&:before {
				border-color: rgba(187, 187, 187, 0);
				border-top-color: #bbb;
				border-width: 6px;
				margin-left: -6px; } } }

	&:hover {
		ul {
			display: block; } } }

.state-label {
	padding: 6px 8px;
	font-size: 14px;
	font-weight: $weight-semibold;
	border-radius: 4px;
	border: 1px solid transparent;

	&.state-label--new {
		background: $lightest-grey;
		color: $dark-grey; }

	&.state-label--paid {
		border-color: $main-green;
		color: $main-green; }

	&.state-label--sent {
		background: $yellow-color;
		color: #fff; }

	&.state-label--completed {
		background: $main-green;
		color: #fff; }

	&.state-label--cancelled {
		background: $error-color;
		color: #fff; } }

.card {
	border-radius: 2px;
	background-color: $white;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
	border: solid 1px $lighter-grey;
	padding: 20px;

	&.card--large {
		padding: 40px; }

	.card_title {
		margin: 0 0 10px;
		display: block;
		font-weight: $weight-semibold;
		font-size: 20px;
		color: $greyish-brown; }

	&.card-focus {
		animation: cardFocusAnimation linear 600ms;
		animation-iteration-count: 1;
		transform-origin: 50% 50%; } }

@keyframes cardFocusAnimation {
	0% {
		transform: translate(0px, 0px) rotate(0deg); }

	15% {
		transform: translate(-25px, 0px) rotate(-5deg); }

	30% {
		transform: translate(20px, 0px) rotate(3deg); }

	45% {
		transform: translate(-15px, 0px) rotate(-3deg); }

	60% {
		transform: translate(10px, 0px) rotate(2deg); }

	75% {
		transform: translate(-5px, 0px) rotate(-1deg); }

	100% {
		transform: translate(0px, 0px) rotate(0deg); } }

table.table {
	thead {
		tr {
			th {
				font-weight: $weight-semibold;
				padding: 15px;
				border-bottom: 1px solid $lighter-grey; } } }

	tbody, tfoot {
		tr {
			td {
				padding: 15px; } } }

	&.table--full {
		width: 100%; }

	&.table--striped {
		tbody {
			tr {
				td {
					background: $white; }

				&:nth-child(2n+2) {
					td {
						background: $almost-white; } } } } } }

.img {
	&.img--rounded {
		border-radius: 100%; } }

.anchor {
	&.anchor--underline {
		text-decoration: underline; }

	&.anchor--inline {
		display: inline-block; } }

@media all and (max-width: 767px) {
	table.table {
		&.table--responsive {
			thead {
				display: none; }

			tfoot {
				border-top: 2px solid $light-grey; }

			tbody, tfoot {
				tr, td {
					display: block; }

				tr {
					td {
						border-bottom: 1px solid $lightest-grey;

						&:before {
							content: attr(data-th);
							display: block;
							float: left;
							width: 30%;
							font-weight: $weight-bold; }

						&:last-of-type {
							border-bottom: none; } } } }

			&.table--responsive--wide {
				tbody, tfoot {
					tr {
						td {
							&:before {
								width: 35%; } } } } } } }

	.button {
		&.button--full-mobile {
			width: 100%;
			display: block; }

		&.button--huge {
			padding-left: 30px;
			padding-right: 30px; } }

	.input {
		&.input--full-mobile {
			width: 100%;
			display: block; } }

	.select {
		&.select--full-mobile {
			width: 100%;
			display: block; } } }

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
	.select {
		background-image: url(/images/icons/icon-arrow-down-small@2x.png);
		background-size: 22px 4px; } }
