body.index {
	.splash_circle {
		background: #fff;
		width: 250px;
		height: 250px;
		border-radius: 100%;
		text-align: center;
		padding: 72px 10px 0;
		color: #555;

		span {
			font-size: 13px;
			text-transform: uppercase;
			display: block;
			margin-bottom: 20px; }

		small {
			font-size: 20px;
			color: $main-green;
			font-weight: $weight-semibold; }

		strong {
			font-size: 60px;
			font-weight: $weight-bold;
			color: $main-green;
			display: block; } }

	header.header--landing {
		height: 100%;
		height: 100vh;
		position: relative;
		min-height: 760px;
		background: $main-green url(/images/header_pattern.png) no-repeat right 134px;

		.header-nav {
			border-bottom: 1px solid rgba(255,255,255,.3);
			background: transparent;

			ul li a {
				color: #fff;

				&:hover {
					background: #fff;
					color: $main-green; } }

			.hamburger .meat {
				background: #fff !important; } }

		.header_content {
			color: #fff;
			margin-top: 20vh;

			h1 {
				font-size: 50px;
				font-weight: $weight-bold;
				margin: 0 0 20px;
				text-shadow: 0 0 6px rgba(0, 0, 0, .2); }

			p {
				font-size: 17px;
				line-height: 1.8;
				margin: 0 0 50px; }

			a.button {
				box-shadow: 0 0 25px rgba(0, 0, 0, .2); } }

		.header_footer {
			background: rgba($dark-grey, 0.75);
			width: 100%;
			position: absolute;
			left: 0;
			bottom: 0;
			color: #fff;
			text-align: center;
			padding: 55px;
			font-size: 32px;

			&.header_footer--sticky {
				position: fixed;
				top: 0;
				bottom: auto;
				background: rgba($dark-grey, 0.95);
				z-index: 150;
				padding: 30px 15px;
				font-size: 26px; }

			a {
				color: #fff;
				text-decoration: underline; } } }

	.block {
		&.block--one {
			padding: 120px 0;

			h2 {
				font-size: 40px;
				color: $main-green-dark;
				margin: 0 0 20px; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 10px 0 0;

				&.sub_header {
					margin: 0 auto 60px;
					max-width: 500px; } }

			h3 {
				margin: 20px 0 10px;
				color: $main-green-dark;
				font-size: 22px;
				font-weight: normal; } }

		&.block--two {
			color: $white;
			background: url(/images/block-two-bg.jpg) no-repeat center center;
			background-size: cover;
			padding: 200px 0;

			h2 {
				font-weight: 600;
				font-size: 40px;
				line-height: 1.2;
				margin: 0 0 30px; }

			p {
				font-size: 15px;
				line-height: 1.5;
				color: $white;
				margin: 0 0 30px; } }

		&.block--three {
			padding: 150px 0;

			blockquote {
				margin: 0 0 70px;
				padding: 50px 50px 0;
				font-size: 24px;
				line-height: 1.5;
				color: $greyish-brown; }

			span.dietist-name {
				margin: 20px 0 0;
				display: block;
				color: $greyish-brown;
				font-size: 16px; } }

		&.block--four {
			background: $white;
			text-align: center;
			margin: 0 0 150px 0; }

		&.block--five {
			background: $almost-white;

			img {
				float: left;
				width: 30vw;
				height: 20vw; }

			p {
				max-width: 680px;
				font-size: 18px;
				line-height: 1.8;
				color: $greyish-brown;
				margin: 0 auto 20px; }

			.block_content {
				float: right;
				width: 70vw;
				height: 20vw;
				padding: 5vw;
				background: $almost-white; } }

		&.block--six {
			color: $white;
			padding: 190px 0;
			background: url(/images/block-six-bg.jpg) no-repeat center center;
			background-size: cover;

			h2 {
				font-weight: normal;
				font-size: 34px;
				margin: 0 0 30px; }

			p {
				line-height: 1.5;
				font-size: 15px;
				margin: 0 0 50px; } }

		&.block--seven {
			background: $almost-white;
			padding: 90px 0;

			h2 {
				font-weight: 700;
				font-size: 40px;
				color: $greyish-brown;
				margin: 0 0 10px; }

			h3 {
				font-size: 26px;
				color: $greyish-brown;
				margin: 0 0 20px;
				font-weight: normal; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 0 0 20px; } }

		&.block--eight {
			padding: 150px 0;

			img {
				margin-left: -20%; }

			h2 {
				font-weight: 700;
				font-size: 40px;
				color: $main-green-dark;
				margin: 30px 0 20px; }

			p {
				font-size: 15px;
				line-height: 2;
				color: $greyish-brown;
				margin: 0 0 20px; } } }

	.slider_container {
		overflow: hidden;
		position: relative;

		.slider-arrow-left,
		.slider-arrow-right {
			position: absolute;
			z-index: 1;
			cursor: pointer; }

		.slider-arrow-left {
			left: 50px;
			top: 50%;
			margin-top: -22px; }

		.slider-arrow-right {
			right: 50px;
			top: 50%;
			margin-top: -22px; }

		.slider {
			max-width: none;

			.slide_container {
				transition: -webkit-transform 450ms;
				transition: transform 450ms;

				.slide {
					overflow: hidden;
					position: relative;
					float: left;
					width: 100%;
					padding: 10px 0; } } } } }

@media all and (max-width: 1599px) {
	body.index {
		.block {
			&.block--five {
				.block_content {
					padding: 3vw; } } } } }

@media all and (max-width: 1299px) {
	body.index {
		.block {
			&.block--five {
				.block_content {
					padding: 1vw; }

				p {
					line-height: 1.5; } } } } }

@media all and (max-width: 991px) {
	body.index {
		.block {
			&.block--five {
				img,
				.block_content {
					float: none;
					height: auto;
					width: 100%; }

				.block_content {
					padding: 5vw; } }

			&.block--one {
				.block_item {
					h3 {
						line-height: 1.3; }

					p {
						line-height: 1.6;
						font-size: 14px; } } } } } }

@media all and (max-width: 767px) {
	body.index {
		.slider_container {
			.slider-arrow-right {
				right: 5px; }

			.slider-arrow-left {
				left: 5px; } }

		.block {
			&.block--one {
				.block_item {
					margin-bottom: 30px;

					&:last-of-type {
						margin-bottom: 0; } } }

			&.block--three {
				padding: 50px 0;

				blockquote {
					padding: 30px 30px 0;
					font-size: 20px; } }

			&.block--eight {
				padding: 50px 0;

				h2 {
					font-size: 32px; } }

			&.block--seven,
			&.block--six,
			&.block--two,
			&.block--one {
				padding: 50px 0; } }

		header.header--landing {
			height: auto;
			min-height: 0;
			padding: 0 0 50px;
			background-position-y: 96px;

			.header_content {
				margin-top: 50px;

				h1 {
					font-size: 44px; } } } } }
